<template>
  <div class="main" v-if="screen_size">
    <top></top>
    <div class="main_content" :style="this.$store.getters.GetSoWidth">
      <div v-if="this.$store.getters.GetSoWidth.width === '66%'" class="index_player">
        <left></left>
      </div>
      <div :style="this.$store.getters.GetSoWidth" style="float: left;overflow-x: hidden;margin: 0 2%">
        <router-view/>
      </div>
    </div>
    <div style="width: 100%;height: 1px;position: relative;float: left"></div>
    <bottom></bottom>
  </div>

  <div v-if="screen_size">
    <back></back>
  </div>
</template>

<script>
import top from '@/components/top'
import bottom from '@/components/bottom'
import left from '@/components/left'
import back from '@/components/back'

export default ({
  data () {
    return {
      so_width: document.body.clientWidth,
      screen_size: true
    }
  },
  components: {
    top,
    bottom,
    left,
    back
  },
  methods: {
    initial () {
      this.api.initial.personal_info(localStorage.ip, 'Home/index').then(res => {
        const data = res.data.data
        data.user_motto = res.data.data.user_motto.split(',')
        this.$store.commit('UpdateUserDate', data)
      })
    }
  },
  mounted () {
    window.onresize = () => {
      return (() => {
        window.so_width = document.body.clientWidth
        this.so_width = window.so_width
      })()
    }
  },
  watch: {
    so_width (val) {
      // 为了避免频繁触发resize函数导致页面卡顿，使用定时器
      if (!this.timer) {
        // 一旦监听到的screenWidth值改变，就将其重新赋给data里的width_m
        const mainWidth = {
          width: '66%'
        }
        const that = this
        setTimeout(function () {
          if (document.body.clientWidth < 1365) {
            mainWidth.width = '96%'
            this.screen_size = false
            this.screen_size = true
          }
          that.$store.commit('UpdateSoWidth', mainWidth)
          that.timer = true
          setTimeout(function () {
            // 打印screenWidth变化的值
            that.timer = false
          }, 200)
        }, 1)
      }
    }
  },
  created () {
    this.initial()
    const mainWidth = {
      width: '66%'
    }
    this.screen_size = true
    if (document.body.clientWidth < 1365) {
      mainWidth.width = '96%'
      this.screen_size = false
      this.screen_size = true
    }
    this.$store.commit('UpdateSoWidth', mainWidth)
  }
})
</script>

