/**
 * initial模块接口列表
 */

import instance from '@/network/request'
import baseURL from '@/network/ip_address' // 根据需求是否导入qs模块

const initial = {
  // 初始信息
  personal_info (ip, add) {
    return instance.post(baseURL.local + 'blog/personal_info/', {
      ip,
      add
    })
  },
  // 文章列表
  article_page (page) {
    return instance.post(baseURL.local + 'blog/article_list/', {
      page
    })
  },
  // 歌曲列表
  get_music () {
    return instance.get(baseURL.local + 'blog/get_music/')
  },
  // 文章列表
  comment_info (id) {
    return instance.post(baseURL.local + 'blog/comment_info/', {
      id
    })
  },
  // 根据id获取文章
  get_id_article (id) {
    return instance.post(baseURL.local + 'blog/get_id_article/', {
      id
    })
  },
  // 添加评论或者子评论
  new_comment (type, commentEmail, temporaryMottoIp, commentName, articleId, commentContent, temporaryId) {
    return instance.post(baseURL.local + 'blog/new_comment/', {
      type,
      commentEmail,
      temporaryMottoIp,
      commentName,
      articleId,
      commentContent,
      temporaryId
    })
  },
  // 关于我
  about_me () {
    return instance.post(baseURL.local + 'blog/about_me/')
  },
  // 上传图片
  load_img (formData, type) {
    return instance.post(baseURL.local + 'blog/load_img/', {
      formData,
      type
    })
  },
  // 修改图片类型
  update_img (id, type) {
    return instance.post(baseURL.local + 'blog/update_img/', {
      id,
      type
    })
  },
  // 删除图片
  delete_img (id) {
    return instance.post(baseURL.local + 'blog/delete_img/', {
      id
    })
  },
  // 获取图片
  get_img (type) {
    return instance.post(baseURL.local + 'blog/get_img/', {
      type
    })
  },
  // 赞
  add_hits (temporaryId, articleId) {
    return instance.post(baseURL.local + 'blog/add_hits/', {
      temporaryId,
      articleId
    })
  },
  // 获取书籍
  get_book (type, isEnd, page) {
    return instance.post(baseURL.local + 'book/get_book/', {
      type,
      isEnd,
      page
    })
  },
  // 获取书籍
  get_book_len () {
    return instance.post(baseURL.local + 'book/get_book_len/', {})
  },
  // 获取书籍详细详细
  get_book_see (url) {
    return instance.post(baseURL.local + 'book/get_book_see/', {
      url
    })
  },
  // 获取书籍内容
  book_content (url) {
    return instance.post(baseURL.local + 'book/book_content/', {
      url
    })
  },
  update_book_url () {
    return instance.post(baseURL.local + 'book/update_book_url/', {})
  }
}

export default initial
