<template>
  <div class="article" v-for="(article, i) in article_list" :key="article" :id="i"
       :style="{backgroundImage: 'url('+ article.article_image +')'}" @click="open_article(article.id)">
    <div class="article_describe">
      <h3 class="post-title">{{ article.article_title }}</h3>
      <p class="article_detailed">{{ article.article_content }}</p>
    </div>
  </div>

  <el-pagination background layout="prev, pager, next" :total="max_length" :pageSize="10" class="paging"
                 @prev-click="prev_click" @current-change="current_change" @next-click="next_click"
                 :hide-on-single-page="true">
  </el-pagination>
</template>

<script>

export default {
  name: 'index',
  data () {
    return {
      page: 10,
      max_length: 10,
      article_list: [{
        article_time: '',
        article_image: '',
        article_title: '',
        article_content: '',
        article_hits: '',
        article_browse: '',
        Article: ''
      }]
    }
  },
  methods: {
    open_article (value) {
      this.$router.push({
        name: 'content',
        query: {
          id: value
        }
      })
    },
    prev_click () {
      this.page -= 10
      this.heavy()
    },
    current_change (val) {
      this.page = 10 * val
      this.heavy()
    },
    next_click () {
      this.page += 10
      this.heavy()
    },
    heavy () {
      this.api.initial.article_page(this.page).then(res => {
        this.article_list = res.data.data.article
        this.max_length = res.data.data.article_length
      })
    }
  },
  mounted () {
    this.heavy()
  }
}
</script>

