/**
 * admin模块接口列表
 */

import instance from '@/network/request'
import baseURL from '@/network/ip_address' // 根据需求是否导入qs模块

const admin = {
  // 登陆
  admin_login (username, password) {
    return instance.post(baseURL.local + 'login/', {
      username,
      password
    })
  },
  // 文章列表
  update_article (article) {
    return instance.post(baseURL.local + 'blog/update_article/', {
      article
    })
  },
  // 歌曲列表
  insert_article (article) {
    return instance.post(baseURL.local + 'blog/insert_article/', {
      article
    })
  }
}

export default admin
