<template>
  <div class="side_lift">
    <div class="user_main">
      <div class="background_img"
           :style="{backgroundImage: 'url(https://cdn.jsdelivr.net/gh/sxiaoy/css_img/image/left_user.png)'}"></div>
    </div>
    <div class="bulletin">
      <p class="content_text" v-for="text in this.$store.getters.GetUserDate.user_motto" :key="text">{{ text }}</p>
    </div>
    <div class="bulletin">
      <div class="time_box">
        <div class="time_text">今天已经过去{{ time_hour }}小时</div>
        <el-progress :text-inside="true" :stroke-width="24" :percentage="parseInt(time_hour / 24 * 100)"
                     style="width: 90%;margin: 5px 5%"/>
      </div>
      <div class="time_box">
        <div class="time_text">本周已经过去{{ time_day }}天</div>
        <el-progress :text-inside="true" :stroke-width="24" :percentage="parseInt(time_day / 7 * 100)"
                     status="success"
                     style="width: 90%;margin: 5px 5%"/>
      </div>
      <div class="time_box">
        <div class="time_text">本月已经过去{{ time_month }}天</div>
        <el-progress :text-inside="true" :stroke-width="24"
                     :percentage="parseInt(time_month / year_time[time_year] * 100)" status="warning"
                     style="width: 90%;margin: 5px 5%"/>
      </div>
      <div class="time_box">
        <div class="time_text">今年已经过去{{ time_year + 1}}月</div>
        <el-progress :text-inside="true" :stroke-width="24" :percentage="parseInt(time_year/12*100)" status="exception"
                     style="width: 90%;margin: 5px 5%"/>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: 'left',
  data () {
    return {
      time_hour: 0,
      time_day: 0,
      time_month: 0,
      time_year: 0,
      year_time: [31, 28, 31, 30, 31, 30, 31, 31, 30, 31, 30, 31]
    }
  },
  methods: {},
  created () {
    const date = new Date()
    this.time_year = date.getMonth()
    this.time_month = date.getDate()
    this.time_day = date.getDay()
    this.time_hour = date.getHours()
    if (this.time_day === 0) {
      this.time_day = 7
    }
  }
}
</script>

