import { createStore } from 'vuex'

export default createStore({
  state: {
    is_book: false,
    so_width: {
      width: '66%'
    },
    userDate: {
      user_username: '',
      user_img: '',
      user_email: '',
      user_type: '',
      user_motto: ''
    },
    book: {
      chapter: [],
      chapter_url: []
    }
  },
  mutations: {
    UpdateBook (state, book) {
      state.book = book
    },
    UpdateIsBook (state, bool) {
      state.is_book = bool
    },
    UpdateUserDate (state, userDate) {
      state.userDate = userDate
    },
    UpdateSoWidth (state, data) {
      state.so_width = data
    }
  },
  getters: {
    GetBook (state) {
      return state.book
    },
    GetIsBook (state) {
      return state.is_book
    },
    GetUserDate (state) {
      return state.userDate
    },
    GetSoWidth (state) {
      return state.so_width
    }
  },
  actions: {},
  modules: {}
})
