import { createRouter, createWebHistory } from 'vue-router'
import index from '@/pages/index'

const routes = [
  {
    path: '/',
    name: 'index',
    component: index
  },
  {
    path: '/content',
    name: 'content',
    component: () => import('../pages/content')
  },
  {
    path: '/about',
    name: 'about',
    component: () => import('../pages/about')
  },
  {
    path: '/book',
    name: 'book',
    component: () => import('../pages/book')
  },
  {
    path: '/book_see',
    name: 'book_see',
    component: () => import('../pages/book_see')
  },
  {
    path: '/book_content',
    name: 'book_content',
    component: () => import('../pages/book_content')
  }
]

const router = createRouter({
  history: createWebHistory(process.env.BASE_URL),
  routes
})

export default router
