<template>
  <div class="top">
    <el-menu class="el-menu-demo" mode="horizontal" :style="this.$store.getters.GetSoWidth">
      <el-menu-item style="font-size: 25px" @click="this.$router.push({path:'/'})">SY</el-menu-item>
      <el-menu-item @click="this.$router.push({path:'/'})">主页</el-menu-item>
      <el-menu-item @click="this.$router.push({path:'/book'})" v-if="this.$store.getters.GetIsBook">书城</el-menu-item>
      <el-menu-item @click="this.$router.push({path:'/about'})">关于我</el-menu-item>
      <div class="show_more" @mouseover="is_player_hover(true)" @mouseleave="is_player_hover(false)"
           :style="{backgroundImage: 'url('+show_more_img[show_more_index][is_hover]+')'}" @click="is_player"
           v-if="this.$store.getters.GetSoWidth.width === '96%'"
      >

      </div>
      <div v-if="show_more_index===1 && this.$store.getters.GetSoWidth.width === '96%'" class="show_left">
        <left></left>
      </div>
    </el-menu>
  </div>
</template>

<script>
import left from '@/components/left'

export default {
  name: 'top',
  components: {
    left
  },
  data () {
    return {
      is_sidebar: false,
      is_hover: 0,
      show_more_img: [[
        'https://cdn.jsdelivr.net/gh/sxiaoy/css_img/image/more_open.png', 'https://cdn.jsdelivr.net/gh/sxiaoy/css_img/image/more_open_hover.png'
      ], [
        'https://cdn.jsdelivr.net/gh/sxiaoy/css_img/image/more_close.png', 'https://cdn.jsdelivr.net/gh/sxiaoy/css_img/image/more_close_hover.png'
      ]],

      show_more_index: 0
    }
  },
  methods: {
    is_player_hover (type) {
      if (type) {
        this.is_hover = 1
      } else {
        this.is_hover = 0
      }
    },
    is_player () {
      if (this.show_more_index) {
        this.show_more_index = 0
      } else {
        this.show_more_index = 1
      }
    }
  }
}
</script>

